import React, {Component} from 'react'
import './App.css'
import MemberList from './components/MemberList'
import FilterForm from './components/FilterForm'
import stateJSON from './assets/state.json'

let currentPage = 0

class App extends Component {
	constructor(props) {
		super(props)
		this.state = {
			members: [],//normalized data
			visibleFields:["officialName","congresses|0|partyAffiliations|0|name","congresses|0|stateCode","congresses|0|stateDistrict","active","status"], 
			filters:[
				{
					name:"Filter by Party",
					dataPath:"congresses/partyAffiliations/name",
					values:[{"Republican":"Republican"},{"Democrat":"Democrat"},{"Independent":"Independent"}]
				},
				{
					name:"Filter by State",
					dataPath:"congresses/stateCode",
					values:[stateJSON]
				}
			],
			apiCall: 'https://clerkapi.azure-api.net/Members?key=da13cc0dce344b3fa36b500e01602df8',
			//apiCall: '/members.json',
			pagination:{
				totalPages:1,
				resultsPerPage:10,
				count:0
			}
		}
		
		const bindFx = (fx => fx.forEach(name => this[name] = this[name].bind(this)))
		bindFx(["handleChange","fetchUrl","getChildData","nextTen","prevTen"])
	}
	
	componentDidMount() {
		this.fetchUrl(this.state.apiCall+ (this.state.apiCall!=='/members.json' ? ("&$skip=" + currentPage * this.state.pagination.resultsPerPage): "" ) )
	}
	
	getChildData(data){
		currentPage = data
	}
	
	fetchUrl(url){
		fetch(url).then(response => response.json()).then(data => {	
			let normalizedMemberData = []

			let pagination = {}
			pagination.totalPages = data.pagination.number_pages
			pagination.resultsPerPage = data.pagination.per_page
			pagination.count = data.pagination.count
			this.setState({pagination:pagination})
			
			data.results.forEach(result => {
				let newMemberObj = {}
				this.state.visibleFields.forEach(field => {
					let fieldParts = field.split(/\|/gi)
					let newResult = result
					fieldParts.forEach(fieldPart => 
						newResult = newResult[fieldPart]
					)
					let niceFieldName = field.replace(/\|[\d]*?\|/gi,"/")
					newMemberObj[niceFieldName] = newResult
				})
				newMemberObj._id = result._id
				normalizedMemberData.push(newMemberObj)
			})

			this.setState({members:normalizedMemberData})

		})
	}
	
	handleChange(event){
		let inputs = event.target.closest('form').querySelectorAll("select")
		let filters = []
		inputs.forEach(input => {
			if(input.value){
				filters.push(input.getAttribute("data-path") + " eq '" + input.value +"'")
			}
		})
		let filterStr = ""
		for(let i in filters){
			if(i === "0"){filterStr+="&$filter="}else if(i>0){filterStr+=" and "}
			filterStr+=filters[i]
		}
		this.setState(prevState => {
			return{
				apiCall:prevState.apiCall.replace(/&\$filter=.*$/gi,"") + filterStr 
			}
		},()=>{//setState is delayed. i need this callback.
			currentPage = 0
			this.fetchUrl(this.state.apiCall)
		})		
	}
	
	prevTen(event){
		currentPage--
		this.fetchUrl(this.state.apiCall+ "&$skip=" + currentPage * this.state.pagination.resultsPerPage)
	}
	
	nextTen(event){
		currentPage++
		this.fetchUrl(this.state.apiCall+ "&$skip=" + currentPage * this.state.pagination.resultsPerPage)
	}

	render(){
		let prevButton, nextButton
			let classStr = "btn btn-4 btn-4d icon-arrow-left"
			let pageCtrlFunction = this.prevTen
			if(currentPage <= 0){
				classStr += " disabled"
				pageCtrlFunction = null
			}
			prevButton = <button className={classStr} onClick={pageCtrlFunction} >Previous</button>
			
			classStr = "btn btn-4 btn-4c icon-arrow-right"
			pageCtrlFunction = this.nextTen
			if(currentPage >= this.state.pagination.totalPages - 1){
				classStr += " disabled"				
				pageCtrlFunction = null
			}
			nextButton = <button className={classStr} onClick={pageCtrlFunction} >Next</button>
			
		
		
		return (
		<div className="App">
			<div className="header" id="navbar"><a className="header__logo invisible" href="https://clerkpreview.house.gov/" aria-label="Office of the Clerk Logo">Office of the Clerk</a></div>
				<div className="mainContent">
			<h1>Members</h1>
			<div className="formDiv">Filters: 
				<FilterForm filters = {this.state.filters} onChange={this.handleChange}/>
			</div>

			
			<div><strong>{this.state.pagination.count}</strong> resuts found. Showing page <strong>{currentPage+1}</strong> out of <strong>{this.state.pagination.totalPages || 0}</strong> page(s).</div>
			<table>
				<thead><tr>
					<th style={{width:"300px"}}>Name</th>
					<th>Party</th>
					<th>State</th>
					<th>District</th>
					<th>Status</th>
					<th>Active</th>
				</tr></thead>
				<MemberList members = {this.state.members} page={currentPage} getChildData={this.getChildData} />
			</table>	

			<div className="pageControls">
			<span className="prevButton">{prevButton}</span>
			<span className="pageCounter">Page {currentPage+1}</span>
			<span className="nextButton">{nextButton}</span>
			</div>
			</div>			
		</div>)
	}
}

export default App
