import React from 'react'
import memberImageJSON from '../assets/memberImg.json'

class MemberInfo extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			membersData: [],
			memberOutputTemplate: [],
			votesOutputTemplate: [],
			currentVoteResults: 10,
			maxVoteResults:100,
			votesAPICall: "https://clerkapi.azure-api.net/Votes?$filter=superEvent/superEvent/congressNum eq '116' and voteType eq 'YEA-AND-NAY'&key=da13cc0dce344b3fa36b500e01602df8&$skip=",
			scrollInstructions:"Scroll down to load more..."
		}
		
		this.params = this.props.match.params
		const bindFx = (fx => fx.forEach(name => this[name] = this[name].bind(this)))
		bindFx(["fetchVotes", "goBack"])
	}
	goBack(){
		this.props.history.goBack();
	}

	componentDidMount() {
		this.fetchMemberData()
	}
	componentDidUpdate(prevProps, prevState){
		document.addEventListener('scroll', this.trackScrolling);
	}
	fetchMemberData(){
		fetch('https://clerkapi.azure-api.net/Members?$filter=_id%20eq%20%27'+this.params.id+'%27&key=da13cc0dce344b3fa36b500e01602df8').then(response => response.json()).then(data => {
			this.setState({membersData: data.results.filter(member => member._id === this.params.id)[0]})
			let outputTemplate = []
			
			let ctx = memberImageJSON[this.params.id]
			let img = memberImageJSON[this.params.id][Math.floor(Math.random()*ctx.length)]
			
			outputTemplate.push(<div key="img"><img alt={this.state.membersData.officialName + " image"}   className="floatRight" src={img} /></div>)
			
			outputTemplate.push(<div key="status">Status: {this.state.membersData.status===""?"Active":this.state.membersData.status}<br /><br /></div>)
			
			ctx = this.state.membersData.congresses
			for(let key in ctx){
				
				outputTemplate.push(<div key={"congress"+key}><strong>{ctx[key].congressNum}<sup>th</sup> Congress</strong><br />{ctx[key].chamber} {ctx[key].position}<br />{ctx[key].stateCode} {ctx[key].stateDistrict} District <br />
				Elected: {ctx[key].electionMonth}/{ctx[key].electionDay}/{ctx[key].electionYear}
				</div>)
			
				let ctx2 = ctx[key].caucusAffiliations
				for(let key2 in ctx2){
					outputTemplate.push(<div key={"caucus"+key2}><strong><br />Caucus Affiliations:<br /></strong>{ctx2[key2].name}
					</div>)
				}
				
				ctx2 = ctx[key].partyAffiliations
				for(let key2 in ctx2){
					outputTemplate.push(<div key={"party"+key2}><strong><br />Party Affiliations:<br /></strong>{ctx2[key2].name}
					</div>)
				}
				
				ctx2 = ctx[key].committeeAssignments
				for(let key2 in ctx[key].committeeAssignments){
					if(ctx2[key2].committeeName !== ""){//why can't this array just be empty?
						let title = key2 > 0 ? [] : [<strong key="title"><br />Committee Assignments:<br /></strong>]
						outputTemplate.push(<div key={"committee"+key2}>{title}{ctx2[key2].committeeName}
					</div>)
					}
				}
				
				ctx2 = ctx[key].subCommitteeAssignments
				for(let key2 in ctx[key].subCommitteeAssignments){
					let title = key2 > 0 ? [] : [<strong key="title"><br />Sub-committee Assignments:<br /></strong>]
					outputTemplate.push(<div key={"subcommittee"+key2}>{title}{ctx2[key2].subCommitteeName}
					</div>)
				}
			
			}
			
			ctx = this.state.membersData.addresses
			for(let key in ctx){
				if(ctx[key].name === "DCOffice"){
					outputTemplate.push(<div key={"address"+key}><strong><br />Address:<br /></strong>
					{ctx[key].streetAddress}<br />
					{ctx[key].addressLocality}, {ctx[key].addressRegion} {ctx[key].postalCode}<br />
					{ctx[key].telephone}
					</div>)
				}
				
			}

			this.setState({memberOutputTemplate:outputTemplate})
	
		})

		this.fetchVotes(this.state.currentVoteResults)
	}	
	
	fetchVotes(skipValue){
		fetch(this.state.votesAPICall+skipValue).then(response => response.json()).then(data => {
			let outputTemplate = []
						
			this.setState({currentVoteResults:(data.pagination.page+1)* data.pagination.per_page,maxVoteResults:data.pagination.count})
			if(this.state.currentVoteResults === 20){
				outputTemplate.push(<h2 key={"votes" + this.state.currentVoteResults}>Voting History</h2>)
			}
			if(this.state.currentVoteResults > data.pagination.count){
				this.setState({scrollInstructions:"No more results."})
				
			}
			function convertDateStr(dateStr){
				let date = new Date(dateStr.replace(/\.000$/gi,"Z"))
				return date.getUTCMonth()+1 +"/"+ date.getUTCDate() +"/"+ date.getUTCFullYear()
				
			}
			let noResultsCount = 0
			data.results.forEach(event => {
				let ctx = event.members.filter(member=> member.usCongressBio === this.params.id)[0]
				if(!ctx){noResultsCount++;return false}
				let description = event.description ? [<span key={"desc" + this.state.currentVoteResults}>({event.description}).</span>] : []
				outputTemplate.push(<div key={event._id + this.state.currentVoteResults}>
					<em>{convertDateStr(event.endDate)}</em>: Voted "<strong>{ctx.vote}</strong>" {event.name}. {description}<br /><br />
				</div>)
			})
			this.setState(prevState => {
				return{
					votesOutputTemplate:prevState.votesOutputTemplate.concat(outputTemplate)
				}
			},()=>{
				if(noResultsCount>0){
					this.fetchVotes(this.state.currentVoteResults+10)
				}	
			})
				
		})
		
	}
	
	
	
	isBottom(el) {
	  return el.getBoundingClientRect().bottom <= window.innerHeight;
	}
	trackScrolling = () => {
	  const wrappedElement = document.getElementsByTagName('body')[0]
	  if (this.isBottom(wrappedElement)) {
		document.removeEventListener('scroll', this.trackScrolling)
		if(this.state.maxVoteResults > this.state.currentVoteResults){
			this.fetchVotes(this.state.currentVoteResults)
			setTimeout(()=>document.addEventListener('scroll', this.trackScrolling),"500")
		}else{

			this.setState(prevState => {
				return{
					scrollInstructions:"No more results."
				}
			})
			
		}
	  }
	}
	componentWillUnmount() {
		const ctx = this.props.location
		if(ctx.passChildData){
			ctx.passChildData.call(this,ctx.page)
		}
		document.removeEventListener('scroll', this.trackScrolling)
	}

	
	render() {
		return <div>
			<div className="header" id="navbar"><a className="header__logo invisible" href="https://clerkpreview.house.gov/" aria-label="Office of the Clerk Logo">Office of the Clerk</a></div>
				<div className="mainContent">
			<button className="btn btn-4 btn-4d icon-arrow-left" onClick={this.goBack}>Back to Results</button>
			<h1>{this.state.membersData.officialName}</h1> 
			<div id="content">
			{this.state.memberOutputTemplate}
			{this.state.votesOutputTemplate}			
			</div>
			<div style={{height:"200px",textAlign:"center","marginTop":"60px",fontWeight:"bold"}}>{this.state.scrollInstructions}</div>
			</div>
		</div>
	}
}
export default MemberInfo








