import React from "react"

function Filter(props) {
	let options = []
	props.options.values.forEach(option => {
		for(let key in option){
			options.push(<option key={key} value={key}>{option[key]}</option>)
		}		
	})
    return (
	<div>
		<label className="hidden">{props.options.name}: </label>
	<select name={props.id} onChange={props.onChange} data-path={props.options.dataPath}>
			<option value="">{props.options.name}</option>
			{options}
		</select>
	</div>	
    )
}

export default Filter